@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');

body {
  background-color: #181a1b;
}




#waifuImage {
  width: 33%;
  height: auto;
  opacity: 100%;
  transition: opacity 0.5s;
  position: absolute;
  margin-bottom: 50px;
  top: 2vw;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 1vw;
  box-shadow: 0 0 1vw 1vw rgba(0, 0, 0, 0);
}

#desc {
  text-shadow: 0 0 10px #000;
  /* background-color: white; */
  text-align: center;
  margin: auto;
  position: absolute;
  left: 49vw;
  font-size: 1vw;
  font-family: 'Nunito', sans-serif;
}